import Button from '@mui/material/Button';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import IconButton from '@mui/material/IconButton';
import InputAdornment from '@mui/material/InputAdornment';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import Dialog from '@mui/material/Dialog';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import { useState, type ReactElement } from 'react';
import { useForm } from 'react-hook-form';
import xinyinApi from '../xinyin/xinyin-main';
import { useTheme } from '@mui/material/styles';
import { isValidSolanaAddress } from '../utils';
import { addWallet, walletsSelector } from '../store/slice-wallets';
import { useDispatch, useSelector } from 'react-redux';
import { useClusterState } from '../store/cluster-store';

const initialWordCount = 600; // 600
const initialStartIndex = 8; // 8

enum Step {
  ChooseCharset,
  InputXinyinText,
  InputWords32,
}

interface XinyinTxt {
  startIndex: number;
  wordCount: number;
  xinyinText: string;
  words32: string;
  password: string;
  repeatPassword: string;
  walletName: string;
}

export default function XinyinDlg({
  type,
  children,
}: {
  type: 'generate' | 'import';
  children: (props: { triggerOpen: () => void }) => ReactElement;
}) {
  const theme = useTheme();
  const [open, setOpen] = useState(false);
  const [step, setStep] = useState(Step.ChooseCharset);
  const [isGenerating, setIsGenerating] = useState(false);
  const [generatedWords32, setGeneratedWords32] = useState<string>('');
  const [showPassword, setShowPassword] = useState(false);
  const [isImporting, setIsImporting] = useState(false);
  const solanaCluster = useClusterState();
  const wallets = useSelector(walletsSelector);
  const dispatch = useDispatch();
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
    resetField,
    trigger,
    watch,
  } = useForm<XinyinTxt>({
    defaultValues: {
      startIndex: initialStartIndex,
      wordCount: initialWordCount,
      xinyinText: '',
      words32: '',
      password: '',
      repeatPassword: '',
      walletName: '',
    },
  });

  const title = type === 'generate' ? '' : '';

  function handleOpen() {
    setOpen(true);
    setStep(Step.ChooseCharset);
    setIsGenerating(false);
    setGeneratedWords32('');
    setShowPassword(false);
    setIsImporting(false);
    reset({
      startIndex: initialStartIndex,
      wordCount: initialWordCount,
      xinyinText: '',
      words32: '',
      password: '',
      repeatPassword: '',
      walletName: '',
    });
  }

  function handleFormSubmit(data: XinyinTxt) {
    if (type == 'generate' && step == Step.InputXinyinText) {
      setIsGenerating(true);
      xinyinApi
        .generateWords32(data.xinyinText, data.startIndex, data.wordCount)
        .then((w32) => {
          setGeneratedWords32(w32);
          setIsGenerating(false);
        });
    } else if (type == 'import' && step == Step.InputWords32) {
      setIsImporting(true);
      xinyinApi
        .importWords32(
          data.words32,
          data.xinyinText,
          data.startIndex,
          data.wordCount,
          data.password
        )
        .then((solanaAddress) => {
          const w =
            wallets && wallets.find((w) => w.$address === solanaAddress);
          if (w) {
            throw new Error(`'${w.$name}'`);
          }
          if (!solanaAddress || !isValidSolanaAddress(solanaAddress)) {
            throw new Error('');
          }
          dispatch(
            addWallet({
              $address: solanaAddress,
              $name: data.walletName,
              $cluster: solanaCluster,
              $balance: 0,
              $hasKey: true,
              $isMine: true,
            })
          );
          setOpen(false);
        })
        .catch((error) => {
          setIsImporting(false);
          alert(': ' + (error.message || ''));
        });
    } else {
      console.log('submit wrong time...');
    }
  }

  function nextStep() {
    if (step == Step.ChooseCharset) {
      trigger(['startIndex', 'wordCount']).then((r) => {
        if (r) {
          setStep(Step.InputXinyinText);
          setGeneratedWords32('');
        }
      });
    }
    if (type == 'import') {
      if (step == Step.InputXinyinText) {
        trigger('xinyinText').then((r) => {
          if (r) {
            setStep(Step.InputWords32);
            resetField('password');
            resetField('repeatPassword');
          }
        });
      }
    }
  }

  function validateXinyinText(text: string): boolean | string {
    if (!text || text.trim() === '') {
      return '';
    }
    return true;
  }

  function validateWords32(words: string): boolean | string {
    if (!words || words.trim() === '') {
      return '';
    }
    if (words.length !== 32) {
      return '32';
    }
    if (!/^[\u4e00-\u9fa5]+$/.test(words)) {
      return '';
    }
    // if (new Set(words.split("")).size !== 32) {
    //   return "";
    // }
    return true;
  }

  function validateStartIndex(value: number): boolean | string {
    if (isNaN(value)) {
      return '';
    }

    if (value < 1) {
      return '1';
    }

    if (value + watch('wordCount') > 8105) {
      return '8105';
    }

    return true;
  }

  function validateWordCount(value: number): boolean | string {
    if (isNaN(value)) {
      return '';
    }

    if (value <= 500) {
      return '500';
    }

    if (value + watch('startIndex') > 8105) {
      return '8105';
    }
    return true;
  }

  function validateWalletName(name: string): boolean | string {
    if (wallets && wallets.some((w) => w.$name === name)) {
      return '';
    }
    return true;
  }

  return (
    <>
      {children({ triggerOpen: handleOpen })}
      <Dialog
        open={open}
        onClose={() => setOpen(false)}
        maxWidth="sm"
        fullWidth
        disableRestoreFocus={true}
        disableAutoFocus={false}
      >
        <DialogTitle>{title}</DialogTitle>
        <DialogContent>
          <Stack
            component="form"
            spacing={2}
            sx={{ paddingTop: 2 }}
            onSubmit={handleSubmit(handleFormSubmit)}
          >
            {step == Step.ChooseCharset && (
              <>
                <TextField
                  label=""
                  fullWidth
                  autoComplete="off"
                  {...register('startIndex', {
                    valueAsNumber: true,
                    validate: validateStartIndex,
                  })}
                  error={!!errors.startIndex}
                  helperText={
                    errors.startIndex ? errors.startIndex.message : ''
                  }
                />
                <TextField
                  label=""
                  fullWidth
                  autoComplete="off"
                  {...register('wordCount', {
                    valueAsNumber: true,
                    validate: validateWordCount,
                  })}
                  error={!!errors.wordCount}
                  helperText={errors.wordCount ? errors.wordCount.message : ''}
                />
              </>
            )}
            {step == Step.InputXinyinText && (
              <>
                <Typography variant="body2" color="text.secondary">
                  20
                </Typography>
                <Typography
                  variant="body2"
                  color="text.secondary"
                  sx={{
                    marginBottom: 1,
                  }}
                >
                  <span
                    style={{
                      color: theme.palette.error.main,
                      fontWeight: theme.typography.fontWeightBold,
                    }}
                  >
                    &nbsp;
                  </span>
                  
                </Typography>
                <TextField
                  label=""
                  fullWidth
                  autoComplete="off"
                  {...register('xinyinText', { validate: validateXinyinText })}
                  error={!!errors.xinyinText}
                  helperText={
                    errors.xinyinText ? errors.xinyinText.message : ''
                  }
                />
              </>
            )}
            {step == Step.InputWords32 && (
              <>
                <TextField
                  label=""
                  fullWidth
                  autoComplete="off"
                  {...register('words32', {
                    validate: validateWords32,
                  })}
                  error={!!errors.words32}
                  helperText={
                    errors.words32
                      ? errors.words32.message
                      : '32'
                  }
                />
                <TextField
                  label=""
                  fullWidth
                  autoComplete="username"
                  {...register('walletName', {
                    required: '',
                    validate: validateWalletName,
                  })}
                  error={!!errors.walletName}
                  helperText={
                    errors.walletName
                      ? errors.walletName.message
                      : ''
                  }
                />
                <TextField
                  label=""
                  autoComplete="new-password"
                  type={showPassword ? 'text' : 'password'}
                  fullWidth
                  {...register('password', {
                    required: '',
                    minLength: {
                      value: 8,
                      message: '8',
                    },
                  })}
                  error={!!errors.password}
                  helperText={
                    errors.password
                      ? errors.password.message
                      : ''
                  }
                  slotProps={{
                    input: {
                      endAdornment: (
                        <InputAdornment position="end">
                          <IconButton
                            onClick={() => setShowPassword((prev) => !prev)}
                          >
                            {showPassword ? <Visibility /> : <VisibilityOff />}
                          </IconButton>
                        </InputAdornment>
                      ),
                    },
                  }}
                />
                <TextField
                  label=""
                  type={showPassword ? 'text' : 'password'}
                  autoComplete="new-password"
                  fullWidth
                  {...register('repeatPassword', {
                    required: '',
                    validate: (value) =>
                      value === watch('password') || '',
                  })}
                  error={!!errors.repeatPassword}
                  helperText={
                    errors.repeatPassword
                      ? errors.repeatPassword.message
                      : ''
                  }
                />
              </>
            )}
            <DialogActions>
              {step == Step.ChooseCharset && (
                <Button onClick={nextStep}></Button>
              )}
              {step == Step.InputXinyinText && (
                <>
                  <Button onClick={() => setStep(Step.ChooseCharset)}>
                    
                  </Button>
                  {type == 'generate' && (
                    <Button
                      type="submit"
                      variant="contained"
                      disabled={isGenerating}
                    >
                      
                    </Button>
                  )}
                  {type == 'import' && (
                    <Button onClick={nextStep}></Button>
                  )}
                </>
              )}
              {step == Step.InputWords32 && (
                <>
                  <Button onClick={() => setStep(Step.InputXinyinText)}>
                    
                  </Button>
                  <Button
                    type="submit"
                    variant="contained"
                    disabled={isImporting}
                  >
                    
                  </Button>
                </>
              )}
            </DialogActions>
            {type == 'generate' && step == Step.InputXinyinText && (
              <>
                <Typography color="text.secondary" variant="body2">
                  
                  <br />
                  32
                </Typography>
                <Typography color="success" variant="body2">
                  {generatedWords32}
                </Typography>
              </>
            )}
          </Stack>
        </DialogContent>
      </Dialog>
    </>
  );
}

