import { useForm, Controller } from 'react-hook-form';
import { useState, type ReactElement } from 'react';
import { shortSolanaAddress } from '../utils';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Stack from '@mui/material/Stack';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import FormHelperText from '@mui/material/FormHelperText';
import InputLabel from '@mui/material/InputLabel';
import { useSelector } from 'react-redux';
import { walletsSelector } from '../store/slice-wallets';

interface TransferData {
  toAddress: string;
  psw: string;
  sol: number;
}

export default function TransferDlg({
  fromAddress,
  fromName,
  renderOpenBtn,
  onResult,
}: {
  fromAddress: string;
  fromName: string;
  renderOpenBtn: (props: { triggerOpen: () => void }) => ReactElement;
  onResult: (toAddress: string, lamports: number, psw: string) => void;
}) {
  const [open, setOpen] = useState(false);
  const wallets = useSelector(walletsSelector);
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
    watch,
    control,
  } = useForm<TransferData>({
    defaultValues: {
      toAddress: '',
      psw: '',
      sol: 0,
    },
  });

  const handleOpen = () => {
    setOpen(true);
    reset({
      toAddress: '',
      psw: '',
      sol: 0,
    });
  };

  const handleClose = () => {
    setOpen(false);
  };

  const onSubmit = (data: TransferData) => {
    handleClose();
    onResult(data.toAddress, data.sol * 1e9, data.psw);
  };

  return (
    <>
      {renderOpenBtn({ triggerOpen: handleOpen })}
      <Dialog
        open={open}
        onClose={handleClose}
        fullWidth
        disableRestoreFocus={true}
        disableAutoFocus={false}
        aria-labelledby="modal-title"
      >
        <DialogTitle id="modal-title"></DialogTitle>
        <DialogContent>
          <Stack
            component="form"
            id="transfer-form"
            spacing={2}
            sx={{ paddingTop: 2 }}
            onSubmit={handleSubmit(onSubmit)}
          >
            <TextField
              label=""
              value={fromName}
              helperText={shortSolanaAddress(fromAddress)}
              disabled
              autoComplete="username"
            />
            <TextField
              label=""
              type="password"
              autoComplete="current-password"
              {...register('psw', { required: true })}
              error={!!errors.psw}
              helperText={errors.psw ? '' : ''}
            />
            <FormControl fullWidth error={!!errors.toAddress}>
              <InputLabel id="to-address-label"></InputLabel>
              <Controller
                name="toAddress"
                control={control}
                rules={{ required: '' }}
                render={({ field }) => (
                  <Select
                    labelId="to-address-label"
                    label=""
                    {...field}
                  >
                    {wallets.map((wallet) => (
                      <MenuItem key={wallet.$address} value={wallet.$address}>
                        {wallet.$name}
                      </MenuItem>
                    ))}
                  </Select>
                )}
              />
              <FormHelperText>
                {watch('toAddress')
                  ? shortSolanaAddress(watch('toAddress'))
                  : ''}
              </FormHelperText>
            </FormControl>
            <TextField
              label=" ( SOL )"
              {...register('sol', {
                required: true,
                min: 0.0001,
                valueAsNumber: true,
              })}
              error={!!errors.sol}
              helperText={errors.sol ? ' 0.0001' : ''}
            />
            <DialogActions>
              <Button onClick={handleClose}></Button>
              <Button type="submit"></Button>
            </DialogActions>
          </Stack>
        </DialogContent>
      </Dialog>
    </>
  );
}
