import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import { useForm } from 'react-hook-form';
import Collapse from '@mui/material/Collapse';
import Alert from '@mui/material/Alert';
import { useState } from 'react';
import { zodResolver } from '@hookform/resolvers/zod';
import { useNavigate, NavLink } from 'react-router';
import Typography from '@mui/material/Typography';
import { createPasskey, registerWithPasskey } from '../../restful/webauthn';
import { registerSchema, type RegisterInfo } from '../../utils/schema-utils';
import { getMe } from '../../restful/user';

function Register() {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    resolver: zodResolver(registerSchema),
  });
  const [err, setErr] = useState<string | null>(null);
  const [info, setInfo] = useState<string | null>(null);
  const navigate = useNavigate();

  async function onSubmit(data: RegisterInfo) {
    setErr(null);
    setInfo(null);
    try {
      setInfo(' Passkey...');
      const cred = await createPasskey(data.userName, data.displayName);
      setInfo('Passkey , ...');
      const result = await registerWithPasskey(cred);
      setInfo(`: ${result.message}...`);
      await getMe();
      navigate('/');
    } catch (error) {
      setInfo(null);
      setErr(error instanceof Error ? error.message : 'Unknown error');
    }
  }

  return (
    <Stack
      component="form"
      onSubmit={handleSubmit(onSubmit)}
      alignItems="center"
      justifyContent="center"
      sx={{ height: '100vh', width: '100%' }}
    >
      <h1></h1>
      <TextField
        label=""
        variant="outlined"
        margin="normal"
        fullWidth
        placeholder=""
        {...register('userName')}
        error={!!errors.userName}
        helperText={errors.userName ? errors.userName.message : ''}
      />
      <TextField
        label=""
        variant="outlined"
        margin="normal"
        placeholder=""
        fullWidth
        {...register('displayName')}
        error={!!errors.displayName}
        helperText={errors.displayName ? errors.displayName.message : ''}
      />
      <Button variant="contained" color="primary" type="submit">
        
      </Button>
      <NavLink to="/login">
        <Typography variant="body2" color="textSecondary" sx={{ mt: 1 }}>
          
        </Typography>
      </NavLink>
      <Collapse in={!!err} sx={{ width: '100%', marginTop: 2 }}>
        <Alert
          severity="error"
          onClose={() => setErr(null)}
          sx={{ wordBreak: 'break-all' }}
        >
          {err}
        </Alert>
      </Collapse>
      <Collapse in={!!info} sx={{ width: '100%', marginTop: 2 }}>
        <Alert severity="info" onClose={() => setInfo(null)}>
          {info}
        </Alert>
      </Collapse>
    </Stack>
  );
}

export default Register;
